$day-gap: 1px;
$calendar-width: 100px;
$calendar-height: 100px;
$num-day: 7;
$cal-max-width: $calendar-width * $num-day + ($day-gap * $num-day - 1);
$calendar-days:
  "mon" 0,
  "tue" 1,
  "wed" 2,
  "thu" 3,
  "fri" 4,
  "sat" 5,
  "sun" 6;
$day-backgroudColor: rgb(238, 238, 238);
$day-color: rgb(0, 0, 0);
